  display: -ms-flexbox;
  display: flex;
  border-bottom: solid 1px #EEEEEE;
  background-color: #FFFFff;
  height: 0.8rem;
  line-height: 0.8rem;
  font-size: 0.32rem;
  font-family: PingFang-SC-Bold;
  color: #4c4c4c;
}
.header .back {
    position: absolute;
    padding-left: 0.27rem;
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    width: 1rem;
    color: #f28300;
}
.header .back .back-icon {
      font-family: iconfont;
}
.header .title {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    text-align: center;
}



