
.distribution-item {
  position: relative;
  background-color: #ffffff;
  border-radius: 5px;
}
.distribution-item .entName {
    padding-left: 0.18rem;
    font-size: 0.29rem;
    font-family: PingFang-SC-Medium;
    padding-top: 0.25rem;
}
.distribution-item .desc {
    position: relative;
    padding: 0.18rem 0 0.24rem 0.18rem;
    font-size: 0.22rem;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    line-height: 0.35rem;
}
.distribution-item .desc .exceed, .distribution-item .desc .notExceed {
      -webkit-box-flex: 0;
          -ms-flex-positive: 0;
              flex-grow: 0;
      padding: 0 0.15rem;
      border-radius: 0.17rem;
      color: #FFFFff;
}
.distribution-item .desc .exceed {
      background-color: #eb5131;
}
.distribution-item .desc .notExceed {
      background-color: #1dd081;
}
.distribution-item .desc .primary-pollutant {
      -webkit-box-flex: 1;
          -ms-flex-positive: 1;
              flex-grow: 1;
      padding-left: 0.2rem;
      color: #999999;
}
.distribution-item .extend-icon {
    position: absolute;
    top: 0.55rem;
    right: 0.25rem;
    font-size: 0.3rem;
    font-family: iconfont;
    color: #999999;
}
.distribution-item .extend {
    position: relative;
    padding: 0.18rem;
    border-top: 2px solid #f0f0f0;
    overflow: hidden;
}
.distribution-item .extend .extend-item {
      float: left;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      width: 33.3%;
      text-align: center;
      margin: 0.18rem 0;
}
.distribution-item .extend .extend-item .extend-item-content {
        -webkit-box-flex: 1;
            -ms-flex: 1;
                flex: 1;
}
.distribution-item .extend .extend-item .extend-item-content .value {
          font-size: 0.49rem;
          font-family: SimHei;
}
.distribution-item .extend .extend-item .extend-item-content .engName, .distribution-item .extend .extend-item .extend-item-content .cnName {
          font-size: 0.22rem;
          font-family: PingFang-SC-Medium;
}
.distribution-item .extend .extend-item .extend-item-content .engName {
          padding: 0.15rem 0 0.08rem 0;
}
.distribution-item .extend .extend-item .extend-item-content .cnName {
          color: #999999;
}
.distribution-item .extend .extend-item .extend-item-split {
        -ms-flex-preferred-size: 0.012rem;
            flex-basis: 0.012rem;
        margin-top: 0.46rem;
        height: 0.45rem;
        background-color: #f28300;
}



